.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  width: 30%;
  height: 20%;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.title {
  margin-bottom: 20px;
}

.buttons {
  display: flex;
  justify-content: space-evenly;
}

.btn {
  width: 60px;
  height: 30px;
  font-weight: 500;
  border-radius: 50%;
}

@media (max-width: 767px) {
  .content {
    width: 80%;
  }
}
